  dt a {
    font-family: "Oxygen", Arial, Verdana, sans-serif;
    font-weight: 700;
    font-style: normal;
    text-decoration: none;
    color: #0072c5; }
    dt a:after {
      font-family: "Font Awesome 5 Free";
      font-weight: 600;
      font-style: normal;
      display: inline-block;
      margin-left: 10px;
      font-size: 20px;
      content: '\f054'; }
